<template>
  <div class="auditOrder">
    <div class="nav">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ name: 'auditOrder' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="tab">
      <el-tabs v-model="activeName" type="border-card" @tab-click="handleClick">
        <el-tab-pane label="" name="first">
          <div class="firstTab tabCont">
            <div class="search">
              <span></span>
              <el-select v-model="platform" placeholder="" style="margin-right: 20px;width:180px;">
                <el-option label="" value="">
                </el-option>
                <el-option label="" value="0">
                </el-option>
                <el-option label="" value="1">
                </el-option>
                <el-option label="" value="2">
                </el-option>
              </el-select>
              <span></span>
              <el-select v-model="shopName" placeholder="" style="margin-right: 20px;width:180px;">
                <el-option label="" value="">
                </el-option>
                <el-option v-for="(item,index) in shopList" :key="index" :label="item.shopName" :value="item.shopId">
                </el-option>
              </el-select>
              <el-input placeholder="" v-model="searchKey" style="width:350px;">
                <template slot="prepend">
                  <el-select v-model="searchKeyType" placeholder="" style="width:120px;">
                    <el-option label="" value="">
                    </el-option>
                    <el-option label="" value="task">
                    </el-option>
                    <el-option label="" value="keyword">
                    </el-option>
                  </el-select>
                </template>
              </el-input>
              <button class="btn" style="margin-left:40px;" @click="getTask"></button>
            </div>
            <el-button plain v-if="0">
              <span style="font-weight:600">excel</span>
            </el-button>
            <noCont v-if="toCheckOrderList.length===0"></noCont>
            <div class="tabItem" v-for="(item, index) in toCheckOrderList" :key="index">
              <ul class="head">
                <li style="width:20%">
                  <i :class="{ 'jdIcon':item.shopType==0, 'taobaoIcon':item.shopType==1, 'tianmaoIcon': item.shopType==2}"></i>
                  <strong>&nbsp;&nbsp;{{ item.shopName }}</strong>
                </li>
                <li style="width:30%">
                  <span class="gray">: </span>
                  <span>{{ item.buyerTaskId }}</span>
                </li>
                <li style="width:20%">
                  <span class="gray">: </span>
                  <span>{{ item.favorTaskType == 0 ? '' : item.favorTaskType == 1 ? '' : item.favorTaskType == 2 ? '' : '' }}</span>
                </li>
                <li style="width:20%">
                  <span class="gray">: </span>
                  <span>{{ item.submitTime }}</span>
                </li>
              </ul>
              <ul class="cont">
                <li>
                  <img :src="item.productPicUrl" alt="pic">
                  <div>
                    <span>&nbsp;&nbsp;{{ item.productName }}</span>
                    <a class="blue" :href="item.productUrl" target="_blank"></a>
                    <p>&nbsp;&nbsp;{{ item.taskDayId }}</p>
                  </div>
                </li>
                <li>
                  <a @click="lookImg((JSON.parse(item.realOrderPicUrl))[0] || '')">
                    <img :src="(JSON.parse(item.realOrderPicUrl))[0] || ''" alt="pic">
                  </a>
                  <div>
                    <span>{{ item.buyerName }}</span>
                    <span>{{ item.realOrderPrice }}</span>
                    <p>{{ item.realOrderId }}&nbsp;&nbsp;
                      <span class="blue copy" :data-clipboard-text='item.realOrderId' @click="doCopy"></span>
                    </p>
                    <p>{{ item.jdUserName }}</p>
                    <p>{{ item.telephone }}</p>
                  </div>
                </li>
                <li>
                  <p>
                    <button class="btn btn-small" @click="toConfirm(item.favorTaskType,item.buyerTaskId)"></button>
                  </p>
                  <p>
                    <button class="btn btn-small disabled" @click="toReject(item.buyerTaskId)"></button>
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <div class="secondTab tabCont">
            <div class="search">
              <span></span>
              <el-select v-model="platform" placeholder="" style="margin-right: 20px;width:180px;">
                <el-option label="" value="">
                </el-option>
                <el-option label="" value="0">
                </el-option>
                <el-option label="" value="1">
                </el-option>
                <el-option label="" value="2">
                </el-option>
              </el-select>
              <span></span>
              <el-select v-model="shopName" placeholder="" style="margin-right: 20px;width:180px;">
                <el-option label="" value="">
                </el-option>
                <el-option v-for="(item,index) in shopList" :key="index" :label="item.shopName" :value="item.shopId">
                </el-option>
              </el-select>
              <el-input placeholder="" v-model="searchKey" style="width:350px;">
                <template slot="prepend">
                  <el-select v-model="searchKeyType" placeholder="" style="width:120px;">
                    <el-option label="" value="">
                    </el-option>
                    <el-option label="" value="task">
                    </el-option>
                    <el-option label="" value="keyword">
                    </el-option>
                  </el-select>
                </template>
              </el-input>
              <button class="btn" style="margin-left:40px;" @click="getTask"></button>
            </div>
            <noCont v-if="toFavorList.length===0"></noCont>
            <div class="tabItem" v-for="(item, index) in toFavorList" :key="index">
              <ul class="head">
                <li style="width:20%">
                  <i :class="{ 'jdIcon':item.shopType==0, 'taobaoIcon':item.shopType==1, 'tianmaoIcon': item.shopType==2}"></i>
                  <strong>&nbsp;&nbsp;{{ item.shopName }}</strong>
                </li>
                <li style="width:30%">
                  <span class="gray">: </span>
                  <span>{{ item.buyerTaskId }}</span>
                </li>
                <li style="width:20%">
                  <span class="gray">: </span>
                  <span>{{ item.favorTaskType == 0 ? '' : item.favorTaskType == 1 ? '' : item.favorTaskType == 2 ? '' : '' }}</span>
                </li>
                <li style="width:20%">
                  <span class="gray">: </span>
                  <span>{{ item.submitTime }}</span>
                </li>
              </ul>
              <ul class="contType2">
                <li>
                  <img :src="item.productPicUrl" alt="pic">
                  <div>
                    <p>{{ item.buyerName }}</p>
                    <p>{{ item.jdUserName }}</p>
                    <p>{{ item.telephone }}</p>
                    <p>{{ item.taskDayId }}</p>
                  </div>
                </li>
                <li>
                  <a @click="lookImg((JSON.parse(item.realOrderPicUrl))[0] || '')">
                    <img :src="(JSON.parse(item.realOrderPicUrl))[0] || ''" alt="pic">
                  </a>
                  <div>
                    <p></p>
                    <p class="red">{{ item.realOrderId }}</p>
                    <p>
                      <span class="red">{{ item.realOrderPrice }}</span>
                    </p>
                  </div>
                </li>
                <li>
                  <div class="evaluateText">
                    <b>{{ item.favorTaskType == 0 ? '' : item.favorTaskType == 1 ? '' : item.favorTaskType == 2 ? '' : '' }}</b>
                    <p>{{ item.sellerFavor || '' }}</p>
                  </div>
                  <div class="imgs" v-if="item.favorTaskType == 2">
                    <a @click="lookImg(m)" v-for="(m, i) in (JSON.parse(item.sellerFavorPicUrl) || [])" :key="i">
                      <img :src="m" alt="pic">
                    </a>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="third">
          <div class="thirdTab tabCont">
            <div class="search">
              <span></span>
              <el-select v-model="platform" placeholder="" style="margin-right: 20px;width:180px;">
                <el-option label="" value="">
                </el-option>
                <el-option label="" value="0">
                </el-option>
                <el-option label="" value="1">
                </el-option>
                <el-option label="" value="2">
                </el-option>
              </el-select>
              <span></span>
              <el-select v-model="shopName" placeholder="" style="margin-right: 20px;width:180px;">
                <el-option label="" value="">
                </el-option>
                <el-option v-for="(item,index) in shopList" :key="index" :label="item.shopName" :value="item.shopId">
                </el-option>
              </el-select>
              <el-input placeholder="" v-model="searchKey" style="width:350px;">
                <template slot="prepend">
                  <el-select v-model="searchKeyType" placeholder="" style="width:120px;">
                    <el-option label="" value="">
                    </el-option>
                    <el-option label="" value="task">
                    </el-option>
                    <el-option label="" value="keyword">
                    </el-option>
                  </el-select>
                </template>
              </el-input>
              <button class="btn" style="margin-left:40px;" @click="getTask"></button>
            </div>
            <noCont v-if="rejectOrderList.length===0"></noCont>
            <div class="tabItem" v-for="(item, index) in rejectOrderList" :key="index">
              <ul class="head">
                <li style="width:20%">
                  <i :class="{ 'jdIcon':item.shopType==0, 'taobaoIcon':item.shopType==1, 'tianmaoIcon': item.shopType==2}"></i>
                  <strong>&nbsp;&nbsp;{{ item.shopName }}</strong>
                </li>
                <li style="width:30%">
                  <span class="gray">: </span>
                  <span>{{ item.buyerTaskId }}</span>
                </li>
                <li style="width:20%">
                  <span class="gray">: </span>
                  <span>{{ item.favorTaskType==0 ? '' : item.favorTaskType==1 ? '' : item.favorTaskType==2 ? '' : '' }}</span>
                </li>
                <li style="width:20%">
                  <span class="gray">: </span>
                  <span>{{ item.submitTime }}</span>
                </li>
              </ul>
              <ul class="cont contType3">
                <li>
                  <img :src="item.productPicUrl" alt="pic">
                  <div>
                    <span>&nbsp;&nbsp;{{ item.productName }}</span>
                    <a class="blue" :href="item.productUrl" target="_blank"></a>
                    <p>&nbsp;&nbsp;{{ item.taskDayId }}</p>
                  </div>
                </li>
                <li>
                  <a @click="lookImg((JSON.parse(item.realOrderPicUrl))[0] || '')">
                    <img :src="(JSON.parse(item.realOrderPicUrl))[0] || ''" alt="pic">
                  </a>
                  <div>
                    <span>{{ item.buyerName }}</span>
                    <span>{{ item.realOrderPrice }}</span>
                    <p>{{ item.realOrderId }}&nbsp;&nbsp;
                      <span class="blue copy" :data-clipboard-text="item.realOrderId" @click="doCopy"></span>
                    </p>
                    <p>{{ item.jdUserName }}</p>
                    <p>{{ item.telephone }}</p>
                  </div>
                </li>
                <li>
                  <div>
                    <b class="gray"></b>
                    <el-tooltip popper-class="tooltipItem" effect="dark" :content="item.rejectReason || ''" placement="top" style="400px">
                      <p>{{ item.rejectReason || '' }}</p>
                    </el-tooltip>
                  </div>
                  <div>
                    <b class="gray"></b>
                    <el-tooltip popper-class="tooltipItem" effect="dark" :content="item.solution || ''" placement="top" style="400px">
                      <p>{{ item.solution || '' }}</p>
                    </el-tooltip>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <div class="pager" v-if="showPager">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNo" :page-sizes="pageSizeArray" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pageTotal">
        </el-pagination>
      </div>
      <div v-show="showLookImg">
        <lookImg :imgUrl="lookImgUrl" @close="showLookImg=false"></lookImg>
      </div>
    </div>
    <div class="alertGroup">
      <!--  -->
      <el-dialog class="alertReturn" title="" :modal-append-to-body="false" :visible.sync="showReturn" width="600px">
        <h2> 12 </h2>
        <ul class="cont">
          <li>
            <span></span>
            <el-select v-model="returnBackObj.reason" placeholder="" style="width:320px;">
              <el-option v-for="(item,index) in rejectReasonList" :key="index" :label="item.rejectReason" :value="item.rejectReason+'&&'+item.step">
              </el-option>
            </el-select>
          </li>
          <li>
            <span></span>
            <el-input type="textarea" :rows="2" placeholder="" v-model="returnBackObj.common" style="width:320px;" resize="none">
            </el-input>
          </li>
          <li>
            <span></span>
            <el-input type="textarea" :rows="4" placeholder="" v-model="returnBackObj.plan" style="width:320px;" resize="none">
            </el-input>
          </li>
        </ul>
        <div class="footBtns">
          <button class="btn" @click="sureToReject"></button>
          <button class="btn disabled" @click="showReturn = false"></button>
        </div>
      </el-dialog>
      <!--  -->
      <el-dialog class="alertConfirm" title="" :modal-append-to-body="false" :visible.sync="showConfirm" width="600px">
        <h3></h3>
        <ul class="cont">
          <li>
            <span></span>
          </li>
          <li v-if="favorType==0">
            <span></span>
          </li>
          <li v-if="favorType==1">
            <span>+</span>
            <div>
              <el-input type="textarea" :rows="3" placeholder="" v-model="confirmObj.evaluteText" style="width:320px;" resize="none">
              </el-input>
            </div>
          </li>
          <li v-if="favorType==2">
            <span>++</span>
            <div>
              <el-input type="textarea" :rows="3" placeholder="" v-model="confirmObj.evaluteText" style="width:384px;" resize="none">
              </el-input>
            </div>
            <div class="imgCont">
              <img v-for="(item, index) in confirmObj.imgArr" :key="index" :src="item" class="avatar">
              <el-upload v-if="confirmObj.imgArr.length<5" class="imgUpload" action="" :show-file-list="false" :http-request="uploadImg" :before-upload="beforeAvatarUpload">
                <i class="add el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
          </li>
        </ul>
        <div class="footBtns">
          <button class="btn" @click="sureToConfirm"></button>
          <button class="btn disabled" @click="showConfirm = false"></button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script type="text/ecmascript-6">
import { pageCommon } from '../../assets/js/mixin'
import { mapGetters } from 'vuex'
import LookImg from '../../base/lookImg/lookImg'
import NoCont from '../../base/noCont/noCont'
import { uploadPromise, uploadFile } from '../../assets/js/upload'
import Clipboard from 'clipboard'
// import Md5 from 'md5'
export default {
  name: 'auditOrder',
  mixins: [pageCommon],
  components: {
    LookImg,
    NoCont
  },
  data () {
    return {
      activeName: 'first',
      showLookImg: false,
      favorType: '',
      lookImgUrl: '',
      platform: '',
      shopName: '',
      shopList: [],
      searchKey: '',
      searchKeyType: '',
      // 
      rejectReasonList: [],
      // 
      toCheckOrderList: [],
      // 
      toFavorList: [],
      // 
      rejectOrderList: [],
      // 
      showReturn: false,
      returnBackObj: {
        reason: '',
        common: '',
        plan: ''
      },
      // 
      showConfirm: false,
      confirmObj: {
        evaluteText: '',
        imgArr: []
      }
    }
  },
  computed: {
    showPager: function () {
      if (this.activeName === 'first' && this.toCheckOrderList.length !== 0) {
        return true
      } else if (this.activeName === 'second' && this.toFavorList.length !== 0) {
        return true
      } else if (this.activeName === 'third' && this.rejectOrderList.length !== 0) {
        return true
      } else {
        return false
      }
    },
    params () {
      return {
        shopType: this.platform,
        shopId: this.shopName,
        keyword: this.searchKey,
        keywordType: this.searchKeyType,
        sellerUserId: this.userInfo.sellerUserId,
        pageNo: this.pageNo,
        pageSize: this.pageSize
      }
    },
    apiUrl () {
      if (this.activeName === 'first') {
        return '/api/seller/order/getToCheckOrderList'
      } else if (this.activeName === 'second') {
        return '/api/seller/order/getCheckedOrderList'
      } else if (this.activeName === 'third') {
        return '/api/seller/order/getRejectOrderList'
      }
    },
    ...mapGetters([
      'userInfo'
    ])
  },
  methods: {
    lookImg (url) {
      this.showLookImg = true
      this.lookImgUrl = url
    },
    doCopy () {
      var clipboard = new Clipboard('.copy')
      clipboard.on('success', (e) => {
        this.$message({
          message: '',
          type: 'success'
        })
      })
    },
    toConfirm (type, buyerTaskId) {
      this.showConfirm = true
      sessionStorage.setItem('__buyerTaskId__', buyerTaskId)
      if (parseInt(type) === 0) {
        this.favorType = 0
      } else if (parseInt(type) === 1) {
        this.favorType = 1
      } else if (parseInt(type) === 2) {
        this.favorType = 2
      }
    },
    uploadImg (img) {
      uploadPromise.then((res) => {
        if (res.statusText === 'OK') {
          uploadFile(res.data, img.file).then((res) => {
            this.confirmObj.imgArr.push(res)
          }).catch(() => {
            this.$message.error('')
          })
        }
      }).catch(() => {
        this.$message.error('')
      })
    },
    // 
    toReject (buyerTaskId) {
      sessionStorage.setItem('__buyerTaskId__', buyerTaskId)
      this.showReturn = true
      this.$ajax.post('/api/config/rejectReason/getReasonList', {
        rejectType: 0 // type : 0   1 
      }).then((data) => {
        if (data.data.code === '200') {
          this.rejectReasonList = data.data.data
        } else {
          this.$message({
            type: 'warning',
            message: data.data.message
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    // 
    sureToReject () {
      let reason = this.returnBackObj.reason.split('&&')[0] || ''
      let step = this.returnBackObj.reason.split('&&')[1] || ''
      this.$ajax.post('/api/seller/order/rejectOrder', {
        buyerTaskId: sessionStorage.getItem('__buyerTaskId__') || '',
        rejectReason: reason,
        comment: this.returnBackObj.common || '',
        solution: this.returnBackObj.plan,
        step: step
      }).then((data) => {
        if (data.data.code === '200') {
          this.showReturn = false
          this.$message({
            type: 'success',
            message: '!'
          })
          this.returnBackObj = {
            reason: '',
            common: '',
            plan: ''
          }
          this.getTask()
        } else {
          this.$message({
            type: 'warning',
            message: data.data.message
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    // 
    sureToConfirm () {
      if (this.confirmObj.evaluteText === '' && parseInt(this.favorType) !== 0) {
        this.$message({
          type: 'warning',
          message: '!'
        })
      } else if (this.confirmObj.imgArr.length === 0 && parseInt(this.favorType) === 2) {
        this.$message({
          type: 'warning',
          message: '!'
        })
      } else {
        let buyerTaskId = sessionStorage.getItem('__buyerTaskId__') || ''
        this.$ajax.post('/api/seller/order/confirmOrder', {
          buyerTaskId: buyerTaskId,
          sellerFavor: this.confirmObj.evaluteText,
          sellerFavorPicUrl: this.confirmObj.imgArr
        }).then((data) => {
          if (data.data.code === '200') {
            this.showConfirm = false
            this.confirmObj = {
              evaluteText: '',
              imgArr: []
            }
            this.$message({
              type: 'success',
              message: '!'
            })
            this.getTask()
          } else {
            this.$message({
              type: 'warning',
              message: data.data.message
            })
          }
        }).catch((err) => {
          console.log(err)
        })
      }
    },
    handleClick (tab, event) {
      this.getTask()
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M
    },
    // 
    getShopList () {
      this.$ajax.post('/api/seller/shop/getShopListBySellerUserId', {
        sellerUserId: this.userInfo.sellerUserId,
        shopType: 3
      }).then((data) => {
        if (data.data.code === '200') {
          this.shopList = data.data.data
        } else {
          this.$message({
            type: 'warning',
            message: data.data.message
          })
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    // 
    setList (data) {
      if (this.activeName === 'first') {
        this.toCheckOrderList = data
      } else if (this.activeName === 'second') {
        this.toFavorList = data
      } else if (this.activeName === 'third') {
        this.rejectOrderList = data
      }
    }
  },
  mounted () {
    this.getShopList()
  }
}
</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
