            span, a, p
              line-height 30px
          &:nth-child(2)
            flex 2
            a>img
              width 60px
              height 106px
              margin-right 20px
            span, p
              line-height 24px
              margin-right 20px
          &:nth-child(3)
            flex 1
            flex-direction column
            justify-content center
            align-items center
            button
              margin-top 12px
              margin-bottom 12px
      .contType2
        display flex
        padding 20px
        font-size 14px
        li
          display flex
          padding-left 30px
          border-left 1px solid #dedede
          &:first-child
            flex 1.5
            padding-left 0
            border-left none
            img
              width 60px
              height 60px
              margin-top 30px
              margin-right 14px
            p
              line-height 30px
          &:nth-child(2)
            flex 1
            a>img
              width 60px
              height 106px
              margin-right 20px
            p
